import './editor.scss';
import {Kjua} from 'react-kjua';
import {InspectorControls, useBlockProps} from "@wordpress/block-editor";
import {PanelBody, SelectControl} from "@wordpress/components";
import {useSelect} from "@wordpress/data";
import {useEffect, useState} from "@wordpress/element";

const {__} = wp.i18n;

export default function Edit({attributes, setAttributes}) {
    const {qr_id, size, format} = attributes;

    const postID = useSelect(
        (select) => {
            const {getCurrentPostId} = select('core/editor');
            return getCurrentPostId();
        },
        []
    );

    const selectedQr = useSelect((select) => {
        return select('core').getEntityRecord('postType', 'qr', qr_id);
    }, [qr_id]);

    const updateData = () => {
        if (selectedQr) {
            let download_format = selectedQr.meta.download_format;

            if ('image' === selectedQr.meta.download_format) {
                download_format = 'png';
            }

            setAttributes(
                {
                    'format': download_format,
                    'size': selectedQr.meta.size,
                }
            );
        }
    }

    const posts = useSelect(
        (select) => {
            const {getEntityRecords} = select('core');
            return getEntityRecords('postType', 'qr', {
                per_page: -1,
                order: 'asc',
                status: 'publish'
            });
        },
        []
    );

    const getSelectablePosts = () => {
        if (!posts) {
            return [];
        }

        const options = [{label: __('Auto-generated QR Code', 'qyrr-code'), value: postID}];

        posts.map(function (post) {
            if (post.title.raw && post.title.raw !== '') {
                options.push({
                    label: post.title.raw,
                    value: post.id,
                });

            }
            return post;
        });

        return options;
    };

    useEffect(() => {
        updateData();

    }, [selectedQr, posts]);

    return (
        <>
            <InspectorControls>
                <PanelBody title={__('QR-Code', 'qyrr-code')} initialOpen={true}>
                    {posts &&
                        <>
                            <SelectControl
                                label={__('Select QR-Code', 'qyrr-code')}
                                value={qr_id}
                                options={getSelectablePosts()}
                                onChange={(value) => {
                                    updateData();
                                    setAttributes({'qr_id': value})
                                }
                                }
                            />

                        </>
                    }
                </PanelBody>
            </InspectorControls>
            <div {...useBlockProps()} id={"qr-code-container"}>
                {posts && qr_id > 0 ?
                    <img width={size + "px"} height={size + "px"}
                         src={qyrr_options.baseurl + '/qyrr/' + qr_id + '/qr-code' + '.' + format}/>
                    :
                    <Kjua
                        render={"image"}
                        text="This is just sample content."
                        image=""
                        minVersion={3}
                        ecLevel={"L"}
                        size={size}
                        fill={"#6804cc"}
                        back={"#FFF"}
                        rounded={0}
                        quiet={0}
                        mode={"plain"}
                        mSize={0}
                        mPosX={0}
                        mPosY={0}
                        label=""
                        fontname={"sans-serif"}
                        fontcolor={"#000"}
                    />
                }
            </div>
        </>
    );
}